<template>
  <div class="jujiao">
    <header-breadcrumb class="breadcrumb" :breadData="$route.matched" />
    <el-row>
        <!-- 部门 -->
    <div
      class="card"
      v-for="(item,index) in allData"
      :key="index"
      >
    <div class="departName">{{item.departName}}<i class="el-icon-more cardIcon"></i></div>
    <!-- 目标 -->
    <div
      class="cardItem"
      v-for="(item2,index2) in item.goals"
      :key="index2"
    >
    <div class="goalName">
      <span>目标{{index2+1}}</span>
     <span>{{item2.goalName}}</span>
    </div>
     <!-- 目标细化 -->
     <el-row v-if="item2.goalType=='progress'">
     <el-col
     :span="24"
      v-for="(item3,index3) in item2.goallist"
      :key="index3"
      :style="{'margin-top':index3==0?'10px':'15px'}"
     >
        <goal-progress
          :data=item3
        />
     </el-col>
     </el-row>
     <el-row v-if="item2.goalType=='pie'">
       <el-col
     :span="12"
      v-for="(item3,index3) in item2.goallist"
      :key="index3"
     >
        <charts-pie
        style="height:150px;"
          :data=item3
        />
     </el-col>
     </el-row>
    </div>
    </div>
    </el-row>
    
  </div>
</template>

<script>
import moment from "moment";
import { HeaderBreadcrumb }from '@/components/breadcrumb'
import { mapGetters } from "vuex";
import { baseTheme } from "@/utils/theme";
import  {jujiaoData} from '@/testData/jujiao'
import GoalProgress from './Components/Progress'
import ChartsPie from './Components/ChartsPie'
// api

export default {
  name: "JuJiao",
  data() {
    return {
      allData:{}
    };
  },
  computed: {
    ...mapGetters(["systemTheme"]),
    systemColor() {
      return baseTheme[this.systemTheme];
    }
  },
  mounted() {
    this.allData = jujiaoData
  },
  methods: {
    
  },
  components: {GoalProgress,ChartsPie,HeaderBreadcrumb}
};
</script>
<style lang="scss" scoped>
.jujiao{
  padding: 20px 15px;
  height: 100%;
  box-sizing: border-box;
  .breadcrumb{
    margin-bottom: 20px;
    margin-left: 5px;
  }
  >.el-row{
    height: calc(100% - 34px);
  }
  .card{
    display: inline-block;
    box-sizing: border-box;
    height: 100%;
    width: calc((100% - 40px) * 0.25);
    background: #F5F5F5;
    padding: 9px;
    margin: 0 5px;
  }
  .departName{
    font-size: 16px;
    color: #1F1F1F;
    letter-spacing: -0.41px;
    margin-bottom: 14px;
  }
  .cardIcon{
    color: #D8D8D8; 
    float: right;
    font-size: 20px;
    line-height: 22px;
    height: 22px;
  }
  .goalName{
    font-size: 14px;
    color: #1F1F1F;
    letter-spacing: -0.41px;
    :nth-child(2){
      margin-left: 20px;
    }
  }
  .cardItem{
    padding: 5px 0 5px 8px;
    background: #FFFFFF;
    border-radius: 2px;
    margin-bottom: 20px;
  }
}
</style>



